import {
  XodActionType,
  XodBaseMessageType,
  XodEventType,
  XodJobType,
} from './message.types';

export interface KafkaCallbackResponse<T = unknown> {
  success: boolean;
  statusCode: number;
  message: string;
  payload?: T;
}

export type KafkaCallbackResponseType<T = string> = (
  additionalMessage?: T,
) => (message: XodBaseMessageType) => Promise<KafkaCallbackResponse>;

export interface XodResponseType {
  type: 'ACTION' | 'EVENT' | 'JOB';
  message: XodActionType | XodEventType | XodJobType;
  result: KafkaCallbackResponse;
  responseSource: string;
}

export interface XodEventResponseType extends XodResponseType {
  type: 'EVENT';
  message: XodEventType;
}

export interface XodActionResponseType extends XodResponseType {
  type: 'ACTION';
  message: XodActionType;
}

export interface XodJobResponseType extends XodResponseType {
  type: 'JOB';
  message: XodJobType;
}

export function isXodResponseType(obj: any): obj is XodResponseType {
  return (
    obj !== undefined &&
    obj !== null &&
    obj.type !== undefined &&
    typeof obj.type === 'string' &&
    (obj.type === 'ACTION' || obj.type === 'EVENT' || obj.type === 'JOB') &&
    obj.message !== undefined &&
    obj.result !== undefined &&
    typeof obj.result === 'object'
  );
}
